import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootobject.model';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {AppSettings} from '../../../../domain/appSettings';
import {Register} from '../../../../domain/models/register/register.model';
import {UrlPath} from '../../../../domain/enums/urlpath';
import {Contacto} from '../../../../domain/models/register/contacto.model';

@Injectable()
export class ContactoService implements IRepository<Contacto> {

  constructor(private http: HttpClient) {
  }

  public create(contacto: Contacto): Observable<RootObject> {
    var request = this.http.post<RootObject>(UrlPath.URL_PATH + `/servicebroker/v1/public/catalog/register/contacto/`, contacto);
    return request;
  }

}
