import {Component, Inject, OnInit} from '@angular/core';
import {ProfileRequest} from '../../../../../domain/models/profile/profilerequest.model';
import {User} from '../../../../../domain/models/register/user.model';
import {UserSession} from '../../../../../domain/models/profile/usersession.model';
import {ProfileBasicToken} from '../../../services/profile/profilebasic.provider';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {RootObject} from '../../../../../domain/models/register/rootobject.model';
import {ProfileBasic} from '../../../../../domain/models/profile/profilebasic.model';
import {PorcentajePerfil} from '../../../../../domain/models/profile/porcentajeperfil.model';
import {ProfilePercentToken} from '../../../services/profile/percent.proveder';

@Component({
  selector: 'app-indicadorconfianza',
  templateUrl: './indicadorconfianza.component.html',
  styleUrls: ['./indicadorconfianza.component.css']
})
export class IndicadorConfianzaComponent implements OnInit {

  public userSession = new UserSession();

  public porcentajePerfil = new PorcentajePerfil();

  constructor(@Inject(ProfilePercentToken) private profilepercent: IRepository<ProfileRequest>) {
    this.userSession = JSON.parse(sessionStorage.getItem('userSession'));
  }

  ngOnInit() {

    var profileRequest = new ProfileRequest();
    profileRequest.vIdThirdPublic = this.userSession.idThirdPublic;
    this.profilepercent.create(profileRequest).subscribe(response => {
      this.porcentajePerfil = response.content;
    });
  }


}
