import { Component, Inject,OnInit } from '@angular/core';
import { NgbActiveModal, NgbModal,NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import {MAT_DIALOG_DATA, MatDialogConfig, MatDialogRef,MatDialog} from "@angular/material";
@Component({
  templateUrl: './Dialog.html',
  styleUrls: ['./Dialog.scss']
})

export class DialogComponent implements OnInit {
  public mensaje: string
  public titulo: string
  public modal : NgbModalRef;
  public imagen:any = '';
  public txtButton: string = '';
  public urlRedirect: string = '';
  public moreInfo: string = '';
  public btnMoreInfo: string = '';
  public btnMoreInfoRedirect: string = '';

  constructor(@Inject(MAT_DIALOG_DATA) public data: any,private modalService: NgbModal,  public dialogRef: MatDialogRef<DialogComponent>) {
  }
  
  ngOnInit() {
  
    if(this.data==1)
    {
      this.mensaje="Nos gusta que todo quede muy bien hecho, por eso vamos a validar tus datos y en menos de 24 horas recibirs un correo electrnico con el que podrs finalizar tu registro.";
      this.titulo=" Muchas gracias!";
      this.txtButton = 'IR A INICIO';
      this.urlRedirect = "/";
      this.imagen = 'https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Check+Illustration.png';
      this.moreInfo = 'Para ms informacin, consulta nuestro';
      this.btnMoreInfo = 'CENTRO DE AYUDA';
      this.btnMoreInfoRedirect = '';
    }
    else if(this.data==2)
    {
      this.mensaje="Por favor, Intenta registrarte de nuevo";
      this.titulo="Uy, algo pas!";
      this.txtButton = 'IR A INICIO';
      this.urlRedirect = "/";
      this.imagen ='https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Warning+no+info.png';
      this.moreInfo = 'Para ms informacin, consulta nuestro';
      this.btnMoreInfo = 'CENTRO DE AYUDA';
      this.btnMoreInfoRedirect = '';
    }
    else if(this.data==3)
    {
      this.mensaje="Revisa tu correo electrnico y activa tu cuenta.";
      this.titulo="Nos falta un paso y completas tu registro!";

      this.txtButton = 'IR A INICIO';
      
      this.imagen ='https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Au+un+solo+paso+Illustration.png';
      this.moreInfo = 'Alguna pregunta?';
      this.btnMoreInfo = 'Contctanos';
      this.btnMoreInfoRedirect = '';
    }
    else if(this.data==5)
    {
      
      this.mensaje="Token de sesin invalido, por favor vuelve e ingresa a la aplicacin.";
      this.titulo="Atencin!";
      this.txtButton = 'INGRESAR';
      this.imagen = "https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Warning+no+info.png";
    }
    else if(this.data.data==5)
    {
      
      this.mensaje="Token de sesin invalido, por favor vuelve e ingresa a la aplicacin.";
      this.titulo="Atencin!";
      this.imagen = "https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Warning+no+info.png";
      this.txtButton = 'INGRESAR';
      this.urlRedirect = this.data.url;
      
      
    }
    else if(this.data.data==4)
    {
      this.mensaje= this.data.mensaje;
      this.titulo=this.data.titulo;
      this.imagen = this.data.imagen;
      this.txtButton = 'INGRESAR';
      
    }
    else if(this.data.data==7)
    {
      this.mensaje= this.data.mensaje;
      this.titulo=this.data.titulo;
      this.imagen = this.data.imagen;
      this.txtButton =  this.data.txtButton;
      this.moreInfo =  this.data.moreInfo;
      
    }
    
  } 
  cerrar() {
    this.dialogRef.close();
  }
}