import {FactoryProvider, InjectionToken} from '@angular/core';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';

import {Contacto} from '../../../../domain/models/register/contacto.model';
import {ContactoService} from './contacto.service';


export const ContactoToken = new InjectionToken<IRepository<Contacto>>('contacto.service');

export class ContactoProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<Contacto>> = ContactoToken;
  public deps: any[] = [ContactoService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<Contacto>[]) => {
    return deps[0];
  };
}
