// tslint:disable: quotemark
import {Component, OnInit, Inject} from '@angular/core';
import {ProfileBasicToken} from '../../../services/profile/profilebasic.provider';

import {ProfileRequest} from '../../../../../domain/models/profile/profilerequest.model';
import {ProfileBasic} from '../../../../../domain/models/profile/profilebasic.model';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {Third} from '../../../../../domain/models/register/third.model';
import {User} from '../../../../../domain/models/register/user.model';
import {RootObject} from '../../../../../domain/models/register/rootobject.model';



import {FinantialModel} from '../../../../../domain/models/profile/finantialmodel.model';
import {CatalogFinantialInfoEnum} from 'src/domain/enums/CatalogFinantialInfoEnum';
import {FinantialInfo} from '../../../../../domain/models/profile/finantialinfo.model';
import {Finantial} from '../../../../../domain/models/profile/finantial.model';
import {CatalogFinantialTypesEnum} from 'src/domain/enums/CatalogFinantialTypeEnum';
import {FinantialToken} from '../../../services/profile/finantial.provider';
import {CommunicationService} from '../../../services/Communication.service';
import {UserSession} from '../../../../../domain/models/profile/usersession.model';
import { MatDialog, MatSnackBar, MatDialogRef } from '@angular/material';
import { DialogComponent } from '../../user/Dialog/DialogComponent';


@Component({
  selector: 'app-finantialstate',
  templateUrl: './finantialstate.component.html',
  styleUrls: ['./finantialstate.component.scss']
})
export class FinantialStateComponent implements OnInit {

  //En esta clase falta hacer la conversion de los modelos temporales a los modelos reales de servicios y branches
  // tslint:disable-next-line: indent
  public userSession = new UserSession();
  public third = new Third();

  // Inicializan valores de pagina
  public finantialModels: FinantialModel[] = [];
  public cuentasPorCobrarClientes = new FinantialInfo();
  public inventarios = new FinantialInfo();
  public activoCorriente = new FinantialInfo();
  public propiedadPlantaYEquipoNeto = new FinantialInfo();
  public otrosActivos = new FinantialInfo();
  public valorizaciones = new FinantialInfo();
  public totalActivos = new FinantialInfo();
  public pasivoCorriente = new FinantialInfo();
  public pasivoNoCorriente = new FinantialInfo();
  public totalPasivo = new FinantialInfo();
  public capital = new FinantialInfo();
  public patrimonioNeto = new FinantialInfo();
  public totalPasivoYPatrimonio = new FinantialInfo();
  public ingresosOperacionales = new FinantialInfo();
  public costoDeVentas = new FinantialInfo();
  public utilidadBruta = new FinantialInfo();
  public gastosOperacionales = new FinantialInfo();
  public utilidadOperacional = new FinantialInfo();
  public ingresosNoOperacionales = new FinantialInfo();
  public gastosNoOperacionales = new FinantialInfo();
  public impuestosYComplemetarios = new FinantialInfo();
  public gananciasYPerdidas = new FinantialInfo();
  public razonCorriente = new FinantialInfo();
  public leverageOCapacidad = new FinantialInfo();
  public nivelDeEndeudamiento = new FinantialInfo();
  public rotacionDeActivoTotal = new FinantialInfo();
  public margenDeUtilidad = new FinantialInfo();
  public rentabilidadBruta = new FinantialInfo();
  public rentabilidadOperacional = new FinantialInfo();
  public rentabilidadNeta = new FinantialInfo();
  public rentabilidadDelPatrimonio = new FinantialInfo();
  public rentabilidadActivoTotal = new FinantialInfo();
  public capitalDeTrabajo = new FinantialInfo();


  constructor(@Inject(ProfileBasicToken) private profileBasic: IRepository<ProfileRequest>
    , @Inject(FinantialToken) private finantialBasic: IRepository<FinantialModel>, public communication: CommunicationService,  private dialog: MatDialog) {


    this.callBasicInformation();
  }

  ngOnInit() {
    var imgFondo = document.getElementById('imgfondoPerfil');
    imgFondo.style.backgroundPosition = '0px -621px';
  }


  callBasicInformation() {

    // this.communication.obs_loadProfile.subscribe(
    // 	(resp: RootObject) => this.fillOutInformation(resp),
    // 	(errno) => console.log('error obs load FinantialState!'+ errno)
    //   );
    this.userSession = JSON.parse(sessionStorage.getItem('userSession'));
    let userProfileRequest = new ProfileRequest();
    userProfileRequest.vIdThirdPublic = this.userSession.idThirdPublic;
    userProfileRequest.user = new User();
    userProfileRequest.user.vIdType = this.userSession.idType;
    userProfileRequest.user.vIdentificationNumber = this.userSession.usIdentificationNumber;
    userProfileRequest.user.vLastName = this.userSession.lastName;

    this.profileBasic.sendRequest(userProfileRequest).subscribe(response => {
      this.fillOutInformation(response);
    });
  }

  fillOutInformation(response: RootObject) {
    let profileBasicResponse: ProfileBasic;
    profileBasicResponse = response.content;
    if (profileBasicResponse !== null) {
      this.third = profileBasicResponse.third;
      this.finantialModels = profileBasicResponse.finantialModels;

      if (profileBasicResponse.finantialModels && profileBasicResponse.finantialModels[0]) {
        profileBasicResponse.finantialModels[0].finantialInfos.forEach(finantialInfo => {
          switch (finantialInfo.vIdInfoTypeFinantial) {
            case CatalogFinantialInfoEnum.CUENTAS_POR_COBRAR_CLIENTES: {
              this.cuentasPorCobrarClientes.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.INVENTARIOS: {
              this.inventarios.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.ACTIVO_CORRIENTE: {
              this.activoCorriente.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.PROPIEDAD_PLANTA_Y_EQUIPO_NETO: {
              this.propiedadPlantaYEquipoNeto.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.OTROS_ACTIVOS: {
              this.otrosActivos.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.VALORIZACIONES: {
              this.valorizaciones.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.TOTAL_ACTIVOS: {
              this.totalActivos.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.PASIVO_CORRIENTE: {
              this.pasivoCorriente.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.PASIVO_NO_CORRIENTE: {
              this.pasivoNoCorriente.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.TOTAL_PASIVO: {
              this.totalPasivo.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.CAPITAL: {
              this.capital.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.PATRIMONIO_NETO: {
              this.patrimonioNeto.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.TOTAL_PASIVO_Y_PATRIMONIO: {
              this.totalPasivoYPatrimonio.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.INGRESOS_OPERACIONALES: {
              this.ingresosOperacionales.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.COSTO_DE_VENTAS: {
              this.costoDeVentas.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.UTILIDAD_BRUTA: {
              this.utilidadBruta.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.GASTOS_OPERACIONALES: {
              this.gastosOperacionales.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.UTILIDAD_OPERACIONAL: {
              this.utilidadOperacional.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.INGRESOS_NO_OPERACIONALES: {
              this.ingresosNoOperacionales.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.GASTOS_NO_OPERACIONALES: {
              this.gastosNoOperacionales.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.IMPUESTOS_Y_COMPLEMETARIOS: {
              this.impuestosYComplemetarios.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.GANANCIAS_Y_PERDIDAS: {
              this.gananciasYPerdidas.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.RAZON_CORRIENTE: {
              this.razonCorriente.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.LEVERAGE_O_CAPACIDAD: {
              this.leverageOCapacidad.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.NIVEL_DE_ENDEUDAMIENTO: {
              this.nivelDeEndeudamiento.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.ROTACION_DE_ACTIVO_TOTAL: {
              this.rotacionDeActivoTotal.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.MARGEN_DE_UTILIDAD: {
              this.margenDeUtilidad.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.RENTABILIDAD_BRUTA: {
              this.rentabilidadBruta.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.RENTABILIDAD_OPERACIONAL: {
              this.rentabilidadOperacional.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.RENTABILIDAD_NETA: {
              this.rentabilidadNeta.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.RENTABILIDAD_DEL_PATRIMONIO: {
              this.rentabilidadDelPatrimonio.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.RENTABILIDAD_ACTIVO_TOTAL: {
              this.rentabilidadActivoTotal.vValue = finantialInfo.vValue;
              break;
            }
            case CatalogFinantialInfoEnum.CAPITAL_DE_TRABAJO: {
              this.capitalDeTrabajo.vValue = finantialInfo.vValue;
              break;
            }
          }
        });

      }

    } else {
      console.log('Empty Data');
    }


  }

  upSertFinantialInfo() {
    let finantialModel = new FinantialModel();
    finantialModel.idThirdPublic = this.userSession.idThirdPublic;
    finantialModel.idUserPublic = this.userSession.idUserPublic;
    let finantial = new Finantial();
    finantial.vIdFinatialType = CatalogFinantialTypesEnum.INDICADORES_FINANCIEROS;
    finantial.vIdThirdPublic = this.third.vIdThirdPublic;
    let finantialInfos: FinantialInfo[];
    this.cuentasPorCobrarClientes.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.CUENTAS_POR_COBRAR_CLIENTES;
    this.inventarios.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.INVENTARIOS;
    this.activoCorriente.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.ACTIVO_CORRIENTE;
    this.propiedadPlantaYEquipoNeto.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.PROPIEDAD_PLANTA_Y_EQUIPO_NETO;
    this.otrosActivos.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.OTROS_ACTIVOS;
    this.valorizaciones.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.VALORIZACIONES;
    this.totalActivos.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.TOTAL_ACTIVOS;
    this.pasivoCorriente.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.PASIVO_CORRIENTE;
    this.pasivoNoCorriente.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.PASIVO_NO_CORRIENTE;
    this.totalPasivo.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.TOTAL_PASIVO;
    this.capital.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.CAPITAL;
    this.patrimonioNeto.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.PATRIMONIO_NETO;
    this.totalPasivoYPatrimonio.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.TOTAL_PASIVO_Y_PATRIMONIO;
    this.ingresosOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.INGRESOS_OPERACIONALES;
    this.costoDeVentas.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.COSTO_DE_VENTAS;
    this.utilidadBruta.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.UTILIDAD_BRUTA;
    this.gastosOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.GASTOS_OPERACIONALES;
    this.utilidadOperacional.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.UTILIDAD_OPERACIONAL;
    this.ingresosNoOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.INGRESOS_NO_OPERACIONALES;
    this.gastosNoOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.GASTOS_NO_OPERACIONALES;
    this.impuestosYComplemetarios.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.IMPUESTOS_Y_COMPLEMETARIOS;
    this.gananciasYPerdidas.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.GANANCIAS_Y_PERDIDAS;
    this.razonCorriente.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.RAZON_CORRIENTE;
    this.leverageOCapacidad.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.LEVERAGE_O_CAPACIDAD;
    this.nivelDeEndeudamiento.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.NIVEL_DE_ENDEUDAMIENTO;
    this.rotacionDeActivoTotal.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.ROTACION_DE_ACTIVO_TOTAL;
    this.margenDeUtilidad.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.MARGEN_DE_UTILIDAD;
    this.rentabilidadBruta.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.RENTABILIDAD_BRUTA;
    this.rentabilidadOperacional.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.RENTABILIDAD_OPERACIONAL;
    this.rentabilidadNeta.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.RENTABILIDAD_NETA;
    this.rentabilidadDelPatrimonio.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.RENTABILIDAD_DEL_PATRIMONIO;
    this.rentabilidadActivoTotal.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.RENTABILIDAD_ACTIVO_TOTAL;
    this.capitalDeTrabajo.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.CAPITAL_DE_TRABAJO;

    finantialInfos = [
      this.cuentasPorCobrarClientes, this.inventarios, this.activoCorriente, this.propiedadPlantaYEquipoNeto,
      this.otrosActivos, this.valorizaciones,
      this.totalActivos, this.pasivoCorriente,
      this.pasivoNoCorriente, this.totalPasivo,
      this.capital, this.patrimonioNeto,
      this.totalPasivoYPatrimonio, this.ingresosOperacionales,
      this.costoDeVentas, this.utilidadBruta,
      this.gastosOperacionales, this.utilidadOperacional,
      this.ingresosNoOperacionales, this.gastosNoOperacionales,
      this.impuestosYComplemetarios, this.gananciasYPerdidas,
      this.razonCorriente, this.leverageOCapacidad,
      this.nivelDeEndeudamiento, this.rotacionDeActivoTotal,
      this.margenDeUtilidad, this.rentabilidadBruta,
      this.rentabilidadOperacional, this.rentabilidadNeta,
      this.rentabilidadDelPatrimonio, this.rentabilidadActivoTotal,
      this.capitalDeTrabajo];
    finantialInfos = finantialInfos.filter(item => item.vValue != null && item.vValue > 0);
    finantialModel.finantialInfos = finantialInfos;
    finantialModel.finantial = finantial;
    finantialModel.matricula = JSON.parse(localStorage.getItem('matricula'));
    this.finantialBasic.create(finantialModel).subscribe(response => {
      //console.log(response);
      this.dialog.open(DialogComponent, { data: {data:7, titulo:'Tu indicador de confianza ha sido actualizado', mensaje:'Con cada actualizacin de datos, DataCrdito Experian premia a sus Partners',imagen:'https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Check+Illustration.png',txtButton:'ACEPTAR'} });

    });

  }
}
