import {ProfileBasicUpsertToken} from '../../../services/profile/profilebasicupsert.provider';
import {ProfileBasicUpSertProvider} from '../../../services/profile/profilebasicupsert.provider';
import {Observable} from 'rxjs';
import {debounceTime, map} from 'rxjs/operators';
import {ServiceInfo} from '../../../../../domain/models/profile/serviceinfo.model';
import {Branch} from '../../../../../domain/models/profile/branch.model';
import {BranchInfo} from '../../../../../domain/models/profile/branchinfo.model';
import {BranchTemp} from '../../../../../domain/models/profile/branchtemp.model';
import {ServiceTemp} from '../../../../../domain/models/profile/servicetemp.mode';
