import {UserSession} from '../../../../../domain/models/profile/usersession.model';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ServiceBaseBean} from '../../../util/service-base-bean';
import {NgxSpinnerService} from 'ngx-spinner';
import {CommunicationService} from '../../../services/Communication.service';
import { MatDialog, MatSnackBar, MatDialogRef } from '@angular/material';
import { DialogComponent } from '../../user/Dialog/DialogComponent';
import { catalog } from 'src/domain/models/register/catalog.model';
import { CatalogToken } from 'src/client/app/services/register/catalog.provider';
import { Content } from "../../../../../domain/models/register/contentCatalog.model";


class ImageSnippet {
  constructor(public src: string, public file: File) {

  }
}

@Component({
  selector: 'app-editthird',
  templateUrl: './editthird.component.html',
  styleUrls: ['./editthird.component.scss']
})
export class EditThirdComponent extends ServiceBaseBean implements OnInit {

  // En esta clase falta hacer la conversion de los modelos temporales a los modelos reales de servicios y branches
  public userSession = new UserSession();
  public third = new Third();
  public nombreComercial = new ThirdInfo();
  public ciudadMunicipio = new ThirdInfo();
  public checkRazonSocial = '';
  public checknombreComercial = '';
  public camaraComercio = new ThirdInfo();
  public estadoMatricula = new ThirdInfo();
  public actividadEconomica = new ThirdInfo();
  public codigoCiiu = new ThirdInfo();
  public descripcionEmpresa = new ThirdInfo();
  public correoElectronico = new ThirdInfo();
  public telefono = new ThirdInfo();
  public direccionComercial = new ThirdInfo();
  public paginaWeb = new ThirdInfo();
  public facebook = new ThirdInfo();
  public twitter = new ThirdInfo();
  public instagram = new ThirdInfo();
  public linkedin = new ThirdInfo();
  public branchModels: BranchModel[] = [];
  public branchTemps: BranchTemp[] = [];
  public servicesModels: ServiceModel[] = [];
  public serviceTemps: ServiceTemp[] = [];
  public photo: string;
  private idUserThirdPublic: string;

  public cityCategory = new CategoryRequest();
  public listCities: CategoryCity[] = [];
  public ciudadThird = {codPadre: '11', nomPadre: 'BOGOTA', codHijo: '11001', nomHijo: 'BOGOTA, D.C.'};


  public selectedFile: ImageSnippet;
  error = false;
  tab1 = true;
  tab2 = false;
  public listActivities: Content[];
  public fromActivitie: boolean = true;

  /**
   * Formulario de validaciones
   */
  registerForm: FormGroup;
  /**
   * Flag envio formulario
   */
  submitted = false;

  constructor(@Inject(ProfileBasicToken) private profileBasic: IRepository<ProfileRequest>,
              @Inject(ProfileBasicUpsertToken) private profileBasicUpsert: IRepository<ProfileBasic>,
              @Inject(CategoryToken) private categoryService: IRepository<CategoryRequest>,
              private formBuilder: FormBuilder,
              private spinner: NgxSpinnerService,private http: HttpClient, private dialog: MatDialog,
              @Inject(CatalogToken) private serviceRegister: IRepository<catalog>) {

    super();

  }
  onKeyUpTextArea(boxInput : HTMLInputElement){
    let length = boxInput.value.length;
    console.log(length);
  }


  ngOnInit() {
    this.callCities();
    this.registerForm = this.formBuilder.group({
      emailFact: ['', [Validators.required, Validators.email]],
      telefonoComercial: ['', [Validators.required]],
      direccionComercial: ['', [Validators.required]],
    }, {});


    this.initTypes();
    //this.addBranchTemp();
    //this.addServiceTemp();
    this.callBasicInformation();
    var catalogEntie = new catalog();
    catalogEntie.catalogId="114";
    this.serviceRegister.create(catalogEntie).subscribe(response => {
     this.listActivities = response.content;
   });
  }


  processFile(imageInput: any) {
    const file: File = imageInput.files[0];
    const reader = new FileReader();

    reader.addEventListener('load', (event: any) => {
      this.selectedFile = new ImageSnippet(event.target.result, file);

      const formData = new FormData();

      formData.append('id', new Date().getSeconds.toString());
      formData.append('file', this.selectedFile.file);

      const basicPhoto = new ProfileBasic();
      basicPhoto.form = formData;
      basicPhoto.idUserThirdPublic = this.userSession.idUserPublic;
//debugger

      this.profileBasicUpsert.sendRequest(basicPhoto).subscribe(response => {
          console.log(response.content);

          localStorage.setItem('photo', response.content);
          this.photo = localStorage.getItem('photo');
        },
        err => {
          console.log(err);
        });

    });

    reader.readAsDataURL(file);
  }

  /**Servicio para traer ciudades */

  private callCities() {
    this.cityCategory.idCategoriaType = '104002';
    this.cityCategory.nombreTree = 'Region';


    this.categoryService.create(this.cityCategory).subscribe(response => {
      this.listCities = response.content;
    });
  }

  /**Inicio Auto-complete*/
  public formatter = (x: { nomHijo: string, nomPadre: string }) => x.nomHijo + ', ' + x.nomPadre;

  search = (text$: Observable<string>) => text$.pipe(
    debounceTime(200),
    map(term => term === '' ? [] : this.listCities.filter(v => v.nomHijo.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10))
  );

  /**Fin Auto-complete*/

  selectactivitie(itemCatalogId:any)
  {
    this.codigoCiiu.vvalue=itemCatalogId;
  }


  private initTypes() {

    this.nombreComercial.vidInfoTypeThird = CatalogThirdInfoProfileEnum.RAZON_SOCIAL_NOMBRE;
    this.nombreComercial.venable = 1;

    this.ciudadMunicipio.vidInfoTypeThird = CatalogThirdInfoProfileEnum.CIUDAD__MUNICIPIO;
    this.ciudadMunicipio.venable = 1;

    this.camaraComercio.vidInfoTypeThird = CatalogThirdInfoProfileEnum.CAMARA_DE_COMERCIO;
    this.camaraComercio.venable = 1;

    this.estadoMatricula.vidInfoTypeThird = CatalogThirdInfoProfileEnum.ESTADO_DE_LA_MATRICULA;
    this.estadoMatricula.venable = 1;

    this.actividadEconomica.vidInfoTypeThird = CatalogThirdInfoProfileEnum.ACTIVIDAD_ECONOMICA;
    this.actividadEconomica.venable = 1;

    this.codigoCiiu.vidInfoTypeThird = CatalogThirdInfoProfileEnum.CODIGO_CIIU;
    this.codigoCiiu.venable = 1;

    this.descripcionEmpresa.vidInfoTypeThird = CatalogThirdInfoProfileEnum.DESCRIPCION_DE_LA_EMPRESA;
    this.descripcionEmpresa.venable = 1;

    this.correoElectronico.vidInfoTypeThird = CatalogThirdInfoProfileEnum.CORREO_ELECTRONICO;
    this.correoElectronico.venable = 1;

    this.telefono.vidInfoTypeThird = CatalogThirdInfoProfileEnum.TELEFONO;
    this.telefono.venable = 1;

    this.direccionComercial.vidInfoTypeThird = CatalogThirdInfoProfileEnum.DIRECCION_COMERCIAL;
    this.direccionComercial.venable = 1;

    this.paginaWeb.vidInfoTypeThird = CatalogThirdInfoProfileEnum.PAGINA_WEB;
    this.paginaWeb.venable = 1;

    this.facebook.vidInfoTypeThird = CatalogThirdInfoProfileEnum.FACEBOOK;
    this.facebook.venable = 1;

    this.twitter.vidInfoTypeThird = CatalogThirdInfoProfileEnum.TWITTER;
    this.twitter.venable = 1;

    this.instagram.vidInfoTypeThird = CatalogThirdInfoProfileEnum.INSTAGRAM;
    this.instagram.venable = 1;

    this.linkedin.vidInfoTypeThird = CatalogThirdInfoProfileEnum.LINKEDIN;
    this.linkedin.venable = 1;

  }


  addBranchTemp() {
    var branchTemp = new BranchTemp();
    branchTemp.ciudad = {'codPadre': '11', 'nomPadre': 'BOGOTA', 'codHijo': '11001', 'nomHijo': 'BOGOTA, D.C.'};
    branchTemp.id = Math.floor((Math.random() * 100000) + 1);
    console.log(this.branchTemps);
    this.branchTemps.push(branchTemp);

  }

  removeBranchTemp(ID: number) {
    if (this.branchTemps.length > 1 && ID > 0) {
      this.branchTemps = this.branchTemps.filter(item => item.id !== ID);
    } else if (this.branchTemps.length == 1) {
      this.branchTemps = this.branchTemps.filter(item => item.id !== ID);
      this.addBranchTemp();
    }
  }


  addServiceTemp() {
    const serviceTemp = new ServiceTemp();
    serviceTemp.id = Math.floor((Math.random() * 100000) + 1);
    this.serviceTemps.push(serviceTemp);
  }

  removeServiceTemp(ID: number) {

    if (this.serviceTemps.length > 1 && ID > 0) {
      this.serviceTemps = this.serviceTemps.filter(item => item.id !== ID);
    } else if (this.serviceTemps.length == 1) {
      this.serviceTemps = this.serviceTemps.filter(item => item.id !== ID);
      this.addServiceTemp();
    }
  }

  // setiar los Infos
  addBranchModel() {
    const branch = new Branch();
    let branchInfos: BranchInfo[] = [];
    const branchCiudad = new BranchInfo();
    branchCiudad.vIdInfoTypeBranch = CatalogBranchInfoEnum.CIUDAD;
    branchCiudad.vEnable = '1';
    const branchDireccion = new BranchInfo();
    branchDireccion.vIdInfoTypeBranch = CatalogBranchInfoEnum.DIRECCION;
    branchDireccion.vEnable = '1';
    const branchTelefono = new BranchInfo();
    branchTelefono.vIdInfoTypeBranch = CatalogBranchInfoEnum.TELEFONO;
    branchTelefono.vEnable = '1';
    branchInfos = [branchCiudad, branchDireccion, branchTelefono];
    const branchModel = new BranchModel();

    branchModel.branch = branch;
    branchModel.branchInfos = branchInfos;

    this.branchModels.push(branchModel);


  }

  private mappingToBranch() {
    this.branchModels = [];
    this.branchTemps.forEach(branchTemp => {
      const branch = new Branch();
      let branchInfos: BranchInfo[] = [];
      const branchModel = new BranchModel();
      branch.vName = branchTemp.nombre;
      branch.vEnable = '1';

      const branchCiudad = new BranchInfo();
      branchCiudad.vIdInfoTypeBranch = CatalogBranchInfoEnum.CIUDAD;
      branchCiudad.vEnable = '1';
      branchCiudad.vValue = branchTemp.ciudad.codHijo;

      const branchDireccion = new BranchInfo();
      branchDireccion.vIdInfoTypeBranch = CatalogBranchInfoEnum.DIRECCION;
      branchDireccion.vEnable = '1';
      branchDireccion.vValue = branchTemp.direccion;

      const branchTelefono = new BranchInfo();
      branchTelefono.vIdInfoTypeBranch = CatalogBranchInfoEnum.TELEFONO;
      branchTelefono.vEnable = '1';
      branchTelefono.vValue = branchTemp.telefono;

      branchInfos = [branchCiudad, branchDireccion, branchTelefono];
      branchModel.branch = branch;
      branchModel.branchInfos = branchInfos;
      this.branchModels.push(branchModel);
    });
  }

  private mappingToService() {
    this.servicesModels = [];
    this.serviceTemps.forEach(serviceTemp => {
      const service = new Service();
      let serviceInfos: ServiceInfo[] = [];
      const serviceModel = new ServiceModel();
      service.vEnable = '1';
      const serviceInfo = new ServiceInfo();
      serviceInfo.vIdInfoTypeServices = CatalogServiceInfoEnum.NOMBRE_SERVICE;
      serviceInfo.vValue = serviceTemp.service;
      serviceInfo.vEnable = '1';
      serviceInfos = [serviceInfo];
      serviceModel.service = service;
      serviceModel.serviceInfos = serviceInfos;
      this.servicesModels.push(serviceModel);
    });
  }


  private mappingToBranchTemp() {

    this.branchTemps = [];
    this.branchModels.forEach(branchModel => {
      const branchTemp = new BranchTemp();
      branchTemp.id = Math.floor((Math.random() * 100000) + 1);
      branchTemp.nombre = branchModel.branch.vName;

      branchModel.branchInfos.forEach(info => {
        switch (info.vIdInfoTypeBranch) {
          case CatalogBranchInfoEnum.CIUDAD:
            branchTemp.ciudad = this.getCity(info.vValue);
            break;
          case CatalogBranchInfoEnum.DIRECCION:
            branchTemp.direccion = info.vValue;
            break;
          case CatalogBranchInfoEnum.TELEFONO:
            branchTemp.telefono = info.vValue;
            break;
        }
      });
      this.branchTemps.push(branchTemp);

      console.log(this.branchTemps);
    });
  }

  private getCity(code: string) {
    var itemCiudad = this.listCities.filter(item => item.codHijo == code);
    return (itemCiudad != null && itemCiudad.length > 0) ? itemCiudad[0] : {
      'codPadre': '11',
      'nomPadre': 'BOGOTA',
      'codHijo': '11001',
      'nomHijo': 'BOGOTA, D.C.'
    };
  }

  private mappingToServiceTemp() {

    this.serviceTemps = [];
    this.servicesModels.forEach(serviceModel => {
      const serviceTemp = new ServiceTemp();
      serviceTemp.id = Math.floor((Math.random() * 100000) + 1);
      serviceModel.serviceInfos.forEach(item => {
        serviceTemp.service = item.vValue;
      });
      this.serviceTemps.push(serviceTemp);
    });
  }


  upsertInfo() {



    this.submitted = true;

    if (this.registerForm.invalid) {
      return;
    }
    this.ciudadMunicipio.vvalue = this.ciudadThird.codHijo;

    var basic = new ProfileBasic();
    this.mappingToBranch();
    this.mappingToService();

    basic.third = this.third;
    basic.thirdInfos = [
      this.nombreComercial,
      this.ciudadMunicipio,
      this.camaraComercio,
      this.estadoMatricula,
      this.actividadEconomica,
      this.codigoCiiu,
      this.descripcionEmpresa,
      this.correoElectronico,
      this.telefono,
      this.direccionComercial,
      this.paginaWeb,
      this.facebook,
      this.twitter,
      this.instagram,
      this.linkedin

    ];
    basic.idUserPublic = this.userSession.idUserPublic;
    basic.idUserThirdPublic = this.idUserThirdPublic;
    basic.branchModels = this.branchModels;
    basic.servicesModels = this.servicesModels;

    this.profileBasicUpsert.create(basic).subscribe(response => {
      //console.log(response);
      this.dialog.open(DialogComponent, { data: {data:7, titulo:'Datos actualizados correctamente',imagen:'https://bepartnerrecursos.s3.us-east-2.amazonaws.com/Ilustraciones+Popups/Check+Illustration.png',txtButton:'ACEPTAR'} });
      this.spinner.hide();
      // 			this.fillOutInformation(response);
    });
  }

  private callBasicInformation() {
    //console.log(this.userProfileRequest);
debugger
    this.userSession = JSON.parse(sessionStorage.getItem('userSession'));
    this.photo = this.userSession.profilePicture;
    let userProfileRequest = new ProfileRequest();
    userProfileRequest.vIdThirdPublic = this.userSession.idThirdPublic;
    userProfileRequest.user = new User();
    userProfileRequest.user.vIdType = this.userSession.idType;
    userProfileRequest.user.vIdentificationNumber = this.userSession.usIdentificationNumber;
    userProfileRequest.user.vLastName = this.userSession.lastName;

    this.profileBasic.sendRequest(userProfileRequest).subscribe(response => {
      console.log(response);
      this.fillOutInformation(response);
      this.spinner.hide();
    });

  }

  private fillOutInformation(response: RootObject) {

    let profileBasicResponse: ProfileBasic;
    profileBasicResponse = response.content;

    if (profileBasicResponse != null) {
      this.branchModels = profileBasicResponse.branchModels;
      this.servicesModels = profileBasicResponse.servicesModels;
      this.third = profileBasicResponse.third;
      this.idUserThirdPublic = profileBasicResponse.idUserThirdPublic;

      profileBasicResponse.thirdInfos.forEach(thirdInfo => {
        switch (thirdInfo.vidInfoTypeThird) {
          case CatalogThirdInfoProfileEnum.RAZON_SOCIAL_NOMBRE: {
            this.nombreComercial = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.CIUDAD__MUNICIPIO: {
            this.ciudadMunicipio = thirdInfo;
            this.ciudadThird = this.getCity(this.ciudadMunicipio.vvalue);
            break;
          }
          case CatalogThirdInfoProfileEnum.CAMARA_DE_COMERCIO: {
            this.camaraComercio = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.ESTADO_DE_LA_MATRICULA: {
            this.estadoMatricula = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.ACTIVIDAD_ECONOMICA: {
            this.actividadEconomica = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.CODIGO_CIIU: {
            this.codigoCiiu = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.DESCRIPCION_DE_LA_EMPRESA: {
            this.descripcionEmpresa = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.CORREO_ELECTRONICO: {
            this.correoElectronico = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.TELEFONO: {
            this.telefono = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.DIRECCION_COMERCIAL: {
            this.direccionComercial = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.PAGINA_WEB: {
            this.paginaWeb = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.FACEBOOK: {
            this.facebook = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.TWITTER: {
            this.twitter = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.INSTAGRAM: {
            this.instagram = thirdInfo;
            break;
          }
          case CatalogThirdInfoProfileEnum.LINKEDIN: {
            this.linkedin = thirdInfo;
            break;
          }
        }
      });


      if (this.branchModels != null && this.branchModels.length > 0) {
        this.mappingToBranchTemp();
      }

      if (this.servicesModels != null && this.servicesModels.length > 0) {
        this.mappingToServiceTemp();
      }
    }

    this.addBranchTemp();
    this.addServiceTemp();


  }

  tabs(opt) {
    //console.log(opt);
    if (opt == 1) {
      this.tab1 = true;
      this.tab2 = false;
    }
    if (opt == 2) {
      this.tab1 = false;
      this.tab2 = true;
    }
  }

  /**
   * Acceso  a los campos de formulario
   * para sus validaciones.
   */
  get formLogin() {
    return this.registerForm.controls;
  }

  /**
   * Captura de procesos de evaluacion
   * del formulario
   */
  onSubmit() {

    this.submitted = true;
    if (this.registerForm.invalid) {
      return;
    }
    this.upsertInfo();
  }
}



