import { FactoryProvider, InjectionToken } from "@angular/core";
import { IRepository } from "../../../../domain/interfaces/irepository.interface";
import { ProfileRequest } from '../../../../domain/models/profile/profilerequest.model';
import {ProfilePercentService} from './percent.service';

export const ProfilePercentToken = new InjectionToken<IRepository<ProfileRequest>>('profilepercent.service');

export class ProfilePercentProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<ProfileRequest>> = ProfilePercentToken;
  public deps: any[] = [ProfilePercentService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<ProfileRequest>[]) => {
    return deps[0]
  };
}
